$dark_green: #3F5D45;
$color_yellow: #FFE180;
$bright_blue: #EAF0ED;

* {
	font-family: "微軟正黑體", serif;
	position: relative;
	box-sizing: border-box;
	padding: 0px;
	margin: 0px; }

html, body {
	width: 100%;
	height: 100%; }

//NAVBAR-------------------------------
.navbar {
	height: 100px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.navbar_left img {
		height: 40px;
		width: 220px;
		margin-top: 30px;
		margin-left: 42px; }

	.navbar_right {
		margin-top: 40px;
		margin-right: 42px;
		display: flex; }


	.navbar_right a {
		margin-left: 60px;
		text-decoration: none;
		font-size: 16px;
		color: $dark_green;
		font-weight: bold;
		text-align: {} }


	#shopping_cart {
		i.material-icons {
			font-size: 24px;
			line-height: 24px; } } }




//HEADER-------------------------------
.header {
	width: 940px;
	height: 496px;
	margin-left: auto;
	margin-right: auto;


	.image_wrap {
		background-image: url("../images/header_image.jpg");
		width: 100%;
		height: 100%;
		background-position: center center;
		background-size: cover; }

	.selection {
		position: absolute;
		display: flex;
		flex-wrap: wrap;
		height: 256px;
		width: 100%;
		justify-content: center;
		top: 405px;

		.items_wrap {
			height: 100%;
			width: 260px;
			top: 0px;
			z-index: 4;
			overflow: hidden;
			user-select: none;
			cursor: pointer;

			p {
				font-size: 24px;
				color: white;
				writing-mode: vertical-rl;
				line-height: 260px;
				height: 260px;
				text-align: center;
				background-color: $dark_green;
				opacity: 0.8;
				transition-duration: 0.2s;
				letter-spacing: 3px; }

			p:hover {
				background-color: $color_yellow;
				color: $dark_green;
				font-weight: bold; } }

		.item {
			top: 0px;
			position: absolute;
			width: 100%;
			background-size: cover;
			background-position: center center;
			filter: blur(5px);
			height: 100%;
			z-index: -2; }

		#recommend_item {
			background-image: url("../images/recommend_item.jpg"); }
		#popular_item {
			background-image: url("../images/popular_item.jpg"); }
		#new_item {
			background-image: url("../images/new_item.jpg"); } } }

//Intro_wrap
.intro_wrap {
	margin-top: 347px;

	.why {
		height: 420px;
		background-color: $bright_blue;

		.content_wrap {
			top: -30px;
			display: flex;
			flex-wrap: wrap;
			width: 905px;
			height: 460px;
			margin-left: auto;
			margin-right: auto;
			justify-content: space-between;

			.img {
				height: 460px;
				width: 460px;
				background-position: center center;
				background-size: cover; }

			.text_img {}

			.textbox {
				width: 256px;
				display: flex;
				flex-direction: row-reverse;

				p {
					writing-mode: vertical-rl;
					height: 280px;
					line-height: 30px;
					font-size: 16px;
					color: #8DA291;
					height: 460px;
					padding-top: 70px;
					padding-bottom: 70px;
					padding-left: 64px; } } } }

	#why_make {
		margin-bottom: 105px;

		#make_textImg {
			background-image: url("../images/make_textImg.svg");
			width: 89px;
			height: 307px;
			right: 0px;
			margin-left: 20px; }

		#make_img {
			background-image: url("../images/make_img.jpg"); } }

	#why_eat {
		#eat_textImg {
			background-image: url("../images/eat_textImg.svg");
			width: 88px;
			height: 303px; }
		#eat_image {
			right: 0px;
			background-image: url("../images/eat_img.jpg"); } }

	.reasonImg_wrap {
		background-image: url("../images/eat_reason.svg");
		background-repeat: no-repeat;
		height: 327px;
		width: 89px;
		margin-left: auto;
		margin-right: auto;
		margin-top: 95px;
		margin-bottom: 80px; } }

//DailySpecial_wrap----------------------------------
.DailySpecial_wrap {
	height: 436px;
	width: 940px;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	user-select: none;

	.today_item {
		width: 300px;

		.top {
			height: 315px;

			.img {
				height: 315px;
				width: 300px;
				background-size: cover;
				background-position: center center; }

			#todaySpecial_img1 {
				background-image: url("../images/todaySpecial1.jpg"); }

			#todaySpecial_img2 {
				background-image: url("../images/todaySpecial2.jpg"); }

			#todaySpecial_img3 {
				background-image: url("../images/todaySpecial3.jpg"); }

			.tag {
				position: absolute;
				top: 0px;
				writing-mode: vertical-rl;
				left: 10px;
				padding: 10px;
				font-size: 16px;
				letter-spacing: 4px;
				background-color: $dark_green;
				color: $bright_blue; }

			.loved {
				background-image: url("../images/loved.svg");
				width: 22px;
				height: 22px;
				position: absolute;
				top: 19px;
				right: 22px; } }

		.bottom {
			height: 121px;
			display: flex;
			flex-wrap: wrap;
			color: $dark_green;

			.item_name {
				font-size: 20px;
				padding: 14px 33px;
				border-right: solid 1px $bright_blue;
				border-left: solid 1px $bright_blue; }

			.price {
				font-size: 20px;
				font-weight: bold;
				padding: 14px 25px 14px 25px;
				border-right: solid 1px $bright_blue; }

			.addToCart {
				font-size: 24px;
				padding: 16px 90px;
				font-weight: bold;
				background-color: $bright_blue; } } } }


//EMAIL Section--------------------------------
.email_wrap {
	margin-top: 60px;
	height: 116px;
	background-color: $dark_green;

	.container {
		width: 76%;
		height: 100%;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around; }


	#icon {
		background-image: url("../images/logo-light.svg");
		height: 40px;
		width: 40px;
		margin: 38px 0px 38px 0px;

		p {
			width: 216px;
			font-size: 24px;
			color: $bright_blue;
			left: 60px;
			line-height: 40px; } }

	input {
		width: 252px;
		height: 56px;
		margin-top: 30px;
		margin-bottom: 30px;
		background-color: $bright_blue;
		border: none; }

	.email_wrap {
		position: absolute;
		height: 56px;
		width: 64px;
		background-color: $bright_blue;
		bottom: 30px;
		left: -64px;

		.material-icons {
			font-family: 'Material Icons';
			position: absolute;
			height: 24px;
			width: 24px;
			line-height: 56px;
			left: 20px;
			color: $dark_green; } }

	.btn {
		position: absolute;
		width: 64px;
		height: 56px;
		background-color: $color_yellow;
		bottom: 30px;
		left: 252px;

		.material-icons {
			height: 16px;
			width: 16px;
			color: $dark_green;
			left: 20px;
			line-height: 56px; } } }

//Footer------------------------
.footer {
	background-color: $bright_blue;


	#info_wrap {
		width: 940px;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	#left_info {

		.icon {
			background-image: url("../images/logotype-lg-dark.svg");
			height: 26px;
			width: 171px;
			margin-top: 39px;
			background-repeat: no-repeat; }

		.shop_info {
			margin-top: 115px;
			font-family: 'Helvetica Neue';
			color: $dark_green;
			font-size: 16px;
			line-height: 24px;
			font-weight: bold; }

		.link {
			display: flex;
			margin-top: 30px;
			margin-bottom: 30px;

			#email {
				background-image: url("../images/ic-line@.svg");
				height: 32px;
				width: 32px;
				margin-right: 8px; }

			#facebook {
				background-image: url("../images/ic-facebook.svg");
				height: 32px;
				width: 32px; } } }
	#right_slogan {
		#slogan {
			margin-left: auto;
			background-image: url("../images/sm-今天是個吃甜點的好日子.svg");
			height: 218px;
			width: 59px;
			margin-top: 30px; }

		#right_reserved {
			margin-top: 46px;
			p {
				color: $dark_green;
				font-size: 16px;
				line-height: 19px;
				font-family: "Times New Roman", Times, serif; } } } }










